



.hidden-title {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.advantages-section {
  background-color: #1e1823;
  padding-top: 20px;
  padding-bottom: 36px;
}

.advantages-list {
  flex-direction: column;
  align-items: center;
  gap: 38px;
  padding-left: 5px;
  padding-right: 32px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .advantages-section {
    padding-top: 20px;
    padding-bottom: 36px;
  }

  .advantages-list {
    flex-flow: wrap;
    align-items: flex-start;
    gap: 60px;
    padding-left: 17px;
    padding-right: 0;
    display: flex;
  }

  .advantages-item {
    flex-basis: calc(50% - 60px);
  }
}

@media screen and (min-width: 1200px) {
  .advantages-section {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .advantages-list {
    flex-flow: row;
    align-items: flex-start;
    gap: 70px;
    padding-left: 35px;
    padding-right: 35px;
  }

  .advantages-item {
    position: relative;
  }

  .advantages-item:after {
    content: "";
    height: 124px;
    border-right: 1px solid rgba(255, 255, 255, .5);
    position: absolute;
    top: 50%;
    right: -18px;
    transform: translateY(-50%);
  }

  .advantages-item:last-child:after {
    display: none;
  }
}

.advantages-icon {
  width: 62px;
  height: 60px;
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.advantages-icon-item {
  width: 28px;
  height: 28px;
}

.advantages-title {
  font-family: var(--main-font-family);
  color: rgba(255, 255, 255, .7);
  padding-top: 22px;
  padding-bottom: 18px;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.2;
}

.advantages-text {
  font-family: var(--main-font-family);
  letter-spacing: -.02em;
  color: rgba(255, 255, 255, .7);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
}





















